import React from 'react';
import { Link } from "react-scroll";
import './Promo.css';
import promologo from '../../images/promo-logo-min.svg';


function Promo () {
  return(
    <section className='promo'>
      <div className='promo__container'>
        <h1 className='promo__title'>    &#8209;.</h1>
        <p className='promo__subtitle'> ,         .</p>
        <Link to='about-project' smooth={true} duration={500} className='promo__link'> </Link>
      </div>
      <img className='promo__img' src={promologo} alt = ''/>

    </section>
  )
}

export default Promo;